@media (max-width: 1199px) {
  .footer__logo {
    margin-top: -2px;
  }
}
@media (max-width: 767px) {
  .footer__logo {
    grid-column: 1/2;
    grid-row: 1/2;
    margin: -20px 0 7px 0;
  }
}

@media (max-width: 1199px) {
  .footer__logo svg {
    width: 83px;
    height: 30px;
  }
}
@media (max-width: 767px) {
  .footer__logo svg {
    width: 67px;
    height: 16px;
  }
}

.footer__list {
  list-style-type: none;
  margin: 1px 0 0 9px;
  padding: 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 40px;
  width: 430px;
}
@media (max-width: 1199px) {
  .footer__list {
    gap: 30px;
    width: 390px;
    margin: 8px 0 0 17px;
  }
}
@media (max-width: 767px) {
  .footer__list {
    font-size: 16px;
    line-height: 19px;
    flex-direction: column;
    gap: 19px;
    grid-column: 1/2;
    grid-row: 2/3;
    margin: 0;
    width: unset;
  }
}

@media (max-width: 767px) {
  .footer__item-hidden {
    display: none;
  }
}

.footer__item a {
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.1em;
  color: #ffffff;
}

.footer__item a:hover {
  border-bottom: 1px solid #ffffff;
}

.footer__list--mobile {
  display: none;
}
@media (max-width: 767px) {
  .footer__list--mobile {
    display: flex;
    flex-wrap: wrap;
    grid-column: 2/3;
    grid-row: 1/3;
  }
}

.footer__social-networks {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  max-width: 100px;
  gap: 11px;
}
@media (max-width: 1199px) {
  .footer__social-networks {
    margin-top: 5px;
    margin-right: -6px;
  }
}
@media (max-width: 767px) {
  .footer__social-networks {
    grid-column: 1/2;
    grid-row: 3/4;
    margin-top: 35px;
    margin-right: 0;
    max-width: unset;
  }
}

.footer__social-networks-item svg {
  fill: #ffffff;
  width: 25px;
  height: 25px;
}

.footer__social-networks-item:hover svg {
  fill: #ffffff;
  opacity: 0.5;
}

